.node-readmore a {
  @extend .button-one;

}

#talk, #awb, #wb{
  clear: both;
  .views-element-container{
    position: relative;
  }
}
#awb{
  max-width: 1200px;
  margin: 0 auto;
}
#wb{
  background-color: $white;
  border-bottom: $base-border;
  border-top: $base-border;
}
.front #main > .container {
  margin-bottom: 0;
}
.front #block-shopsplus-content {
  .content {
    .body {
      h1 {
        padding-top: 36px;
        position: relative;
        font-size: 24px;
        &:before {
          content: "\e901";
          font-family: $icons-font-family;
          position: absolute;
          text-align: center;
          font-size: 28px;
          width: 100%;
          left: 0;
          top: 0;
          color: #c7c5c5;
        }
      }
      p{
        font-size: 26px;
        line-height: 1.3em;
      }
    }
  }
  @include media($tablet) {
    .content {
      .body {
        p{
          font-size: 30px;
        }
      }
    }
  }
  @include media($fullsize) {
    .content {
      .body {
        h1 {
          padding-top: 55px;
          font-size: 28px;
          &:before {
            font-size: 42px;
          }
        }
        p{
          font-size: 36px;
          line-height: 44px;
          margin-bottom: 33px;
        }
      }
    }
  }
}
